import React, { useState } from "react";
import Layout from "@/components/Layout";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import styles from "../styles/category.module.css";

const CategoryPage = () => {
  const [categories, setCategories] = useState([
    { id: 1, name: "Electronics", addedDate: "2023-12-01" },
    { id: 2, name: "Fashion", addedDate: "2023-12-02" },
    { id: 3, name: "Home & Kitchen", addedDate: "2023-12-03" },
  ]);
  const [newCategory, setNewCategory] = useState("");
  const [editCategory, setEditCategory] = useState(null);
  const [showModal, setShowModal] = useState(false);

  const handleAddCategory = () => {
    if (newCategory.trim()) {
      const newId = categories.length ? categories[categories.length - 1].id + 1 : 1;
      const addedDate = new Date().toISOString().split("T")[0]; // Current date
      setCategories([...categories, { id: newId, name: newCategory, addedDate }]);
      setNewCategory("");
    }
  };

  const handleEditCategory = (id, updatedName) => {
    setCategories(
      categories.map((category) =>
        category.id === id ? { ...category, name: updatedName } : category
      )
    );
    setShowModal(false);
  };

  const handleDeleteCategory = (id) => {
    setCategories(categories.filter((category) => category.id !== id));
  };

  return (
    <Layout>
      <div className={styles.container}>
        <div className={styles.contentWrapper}>
          <h1 className={styles.pageTitle}>Category Management</h1>
          <div className={styles.formContainer}>
            <input
              type="text"
              placeholder="Enter a new category"
              value={newCategory}
              onChange={(e) => setNewCategory(e.target.value)}
              className={styles.inputField}
            />
            <button className={styles.addButton} onClick={handleAddCategory}>
              Add Category
            </button>
          </div>

          <table className={styles.table}>
            <thead>
              <tr>
                <th>SL</th>
                <th>Category</th>
                <th>Added Date</th>
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {categories.length ? (
                categories.map((category, index) => (
                  <tr key={category.id}>
                    <td>{index + 1}</td>
                    <td>{category.name}</td>
                    <td>{category.addedDate}</td>
                    <td>
                      <button
                        className={styles.editButton}
                        onClick={() => {
                          setEditCategory(category);
                          setShowModal(true);
                        }}
                      >
                        Edit
                      </button>
                      <button
                        className={styles.deleteButton}
                        onClick={() => handleDeleteCategory(category.id)}
                      >
                        Delete
                      </button>
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan="4" className={styles.noDataText}>
                    No categories available
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>

        {/* Modal for Edit */}
        <Modal show={showModal} onHide={() => setShowModal(false)}>
          <Modal.Header closeButton>
            <Modal.Title>Edit Category</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <input
              type="text"
              className={styles.inputField}
              defaultValue={editCategory?.name}
              onChange={(e) => setEditCategory({ ...editCategory, name: e.target.value })}
            />
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={() => setShowModal(false)}>
              Cancel
            </Button>
            <Button
              variant="primary"
              onClick={() => handleEditCategory(editCategory.id, editCategory.name)}
            >
              Save Changes
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    </Layout>
  );
};

export default CategoryPage;
